@mixin slick-dots-progress($active-color: $red, $bg-color: #888, $i: #{!default}) {
	.slick-dots {
		position: unset;
		display: flex;
		align-items: center;
		justify-content: stretch;
		max-width: 100px;
		margin: 0 auto;
		margin-bottom: 1rem;
		list-style-type: none;
		li {
			position: relative;
			flex-grow: 1;
			height: auto;
			width: 20px;
			margin: 0;
			button {
				position: relative;
				cursor: pointer;
				width: 100%;
				height: 4px;
				margin: 6px 0;
				padding: 0;
				background-color: $active-color $i; 
				text-indent: -9999px;
				&:before {
					display: none;
				}
			}
			&.slick-active {
				~ li {
					button {
						background-color: $bg-color $i;
					}
				}
			}
		}
	}
}

@mixin button-styles {
    width: unset;
    height: unset;
    padding: $button-padding;
    background-color: var(--renoun-global-color-green); 
    color: var(--renoun-global-color-dark); 
    box-shadow: none;
    border-radius: $button-border-radius;
    font-weight: 400;
    font-size: map-get($button-sizes, default);
    text-transform: initial;
}

@mixin slick-arrows-middle {
    padding-left: calc( var(--slick-arrow-dim) * 2.5 );
    padding-right: calc( var(--slick-arrow-dim) * 2.5 );
    .slick-arrow {
        position: absolute;
        transform: none;
    }
    .slick-prev {
        left: calc( var(--slick-arrow-dim) / 2 );
    }
    .slick-next {
        right: calc( var(--slick-arrow-dim) / 2 );
    }
}

@mixin slick-arrows-bottom {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    .slick-arrow {
        position: unset;
        transform: none;
    }
    .slick-prev {
    
    }
    .slick-next {
    
    }
}

@mixin re-icon($icon: '') {
	@if $icon {
	content: "#{$icon}";
	}
	font-family: "renoun-icons";
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	speak: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin gg-icon($icon: '') {
	@if $icon {
	content: "#{$icon}";
	}
	font-family: "ggwny";
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	speak: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin row-margins {
	$gutters: map-get($grid-column-gutter, 'large');
	$gutter: rem-calc( ( $gutters / 2 ) * -1 );
	margin-left: $gutter;
	margin-right: $gutter;
	@include breakpoint(large up) {
		margin-left: $gutter;
		margin-right: $gutter;
	}
}

@mixin social-icons($icon-size:52, $mobile-icon-size: 32, $color: $primary-color, $hover: $secondary-color) {
	$icon-size: $icon-size;
	$mobile-icon-size: $mobile-icon-size;
	$units: px;

	.social-icons {
		a.link {
			&:not(:last-child) {
				margin-right: $global-margin;
			}
			span.network {
				&:before {
					font-size: $icon-size + $units;
					color: $color;
					transition: $link-transition;
				}
			}
			&:hover {
				span.network {
					&:before {
						color: $hover;
					}
				}
			}
		}
	}
	@include breakpoint(medium down) {
		.social-icons {

			a.link {
				margin-right: $global-margin / 1.5;
				span.network {
					&:before {
						font-size: $mobile-icon-size + $units;
					}
				}
				&:hover {
					span.network {
						&:before {

						}
					}
				}
			}
		}
	}
}

@mixin default-bg($size: cover, $pos: center, $repeat: no-repeat) {
	background-size: $size;
	background-position: $pos;
	background-repeat: $repeat;
}

@mixin object-fit() {
	object-fit: cover;
	object-position: center;
	width: 100%;
	height: 100%;
}

@mixin slick-show-first($el) {
	#{$el} {
		display: none;
	}
	#{$el}:first-child {
		display: block;
	}
	&.slick-initialized {
		#{$el} {
			display: block;
		}
	}
}

@mixin responsive-embed($padding) {
	padding-bottom: $padding;
    height: 0;
    max-width: 100%;
    overflow: hidden;
}

@mixin opacity($value){
  $IEValue: $value*100;
  opacity: $value;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity="+$IEValue+")";
  filter: alpha(opacity=$IEValue);
}

@mixin screen-reader {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	word-wrap: normal !important;
	&:focus {
		background-color: #eee;
		clip: auto !important;
		clip-path: none;
		color: #444;
		display: block;
		font-size: 1em;
		height: auto;
		left: 5px;
		line-height: normal;
		padding: 15px 23px 14px;
		text-decoration: none;
		top: 5px;
		width: auto;
		z-index: 100000; /* Above WP toolbar. */
	}
}

@mixin accordion-reset {
	background-color: transparent;
	.accordion-item {
		.accordion-title {
			font-size: unset;
			border: none;
			&:hover, &:focus {
				background-color: transparent;
			}
		}
	}
	.accordion-content {
		background-color: transparent;
		border: none;
	}
}

@mixin tabs-reset {
	ul.tabs {
        border: none;
        background-color: transparent;
        li.tabs-title {
			a {
				&:hover, &:focus, &[aria-selected=true] {
					background: transparent;
				}
			}
        }
    }
	.tabs-content {
        height: 100%;
        border: none;
        background-color: transparent;
        padding: 0;
        .tabs-panel {
            padding: 0;

            .panel-title {

            }
        }
    }
}